<template>
  <div class="search">
    <van-search
            v-model="searchValue"
            placeholder="您想找什么呢？"
            show-action
            @search="onSearch"
            @cancel="onCancel"
            background="#FA8072"
            class="search-bar">
    </van-search>
    <van-notice-bar mode="closeable"
                    :left-icon="require('../assets/png/1.png')"
                    color="#FA8072"
                    background="#F1F1F1"
                    class="search-noticeBar">
      <span>{{ noticeBarText }}</span>
    </van-notice-bar>

    <div class="search-groups">

      <div class="search-groups-card">
        <div class="search-groups-card-title">
          {{ popular.title }}
        </div>
        <div class="search-groups-card-main">
          <div class="search-cell"
               v-for="(data, index) in popular.datas"
               :key="index"
               @click="onCell(data)">
            {{ data }}
          </div>
        </div>
      </div>

      <div class="search-groups-card">
        <div class="search-groups-card-title">
          {{ history.title }}
        </div>
        <div class="search-groups-card-main">
          <div class="search-cell"
               v-for="(data, index) in history.datas"
               :key="index"
               @click="onCell(data)">
            {{ data }}
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "SearchLayout",
    data() {
      return {
        searchValue: '',
        noticeBarText: '象行网：是一个专注于使本地生活信息、旅游信息变得高质、高效的信息服务平台。',
        popular: {
          title: '热门搜索',
          datas: [
            '大学生兼职',
            '暑假工',
            '景区热点',
            '哪好玩',
            '学区房',
            '版纳技术学院',
            '勐仑植物园',
            '鳕鱼酱',
            '景洪大桥',
            '风景',
          ]
        },
        history: {
          title: '近期搜索',
          datas: [
            '大学生兼职',
            '学生',
            '风景',
            '兼职',
            '版纳大学',
            '勐仑植物园',
          ]
        },
      }
    },
    methods: {
      onCancel() {
        this.$router.go(-1)
      },
      onSearch() {
        this.$toast(this.searchValue)
      },
      onCell(data) {
        this.searchValue = data
        this.onSearch()
      },
    }
  }
</script>

<style lang="less">
  @textColor: #FFF;
  @BGColor: #FA8072;

  .search {

    &-bar {
      .van-search__action {
        color: @textColor;
      }
    }
    &-noticeBar {
      margin: 10px 0;
    }
    &-groups {
      margin-top: 10px;
      font-size: 14px;
      &-card {
        &-title {
          line-height: 28px;
          height: 28px;
          border-bottom: 1px solid @BGColor;
        }
        &-main {
          overflow: hidden;
          margin: 5px 0;
        }
      }
    }
    // 提升为 search 全局样式
    .search-cell {
      color: @textColor;
      background-color: @BGColor;
      border-radius: 10px;
      padding: 6px;
      margin: 2px;
      float: left;
    }

  }

</style>